import * as THREE from 'three'
import React, { Suspense, useState, useLayoutEffect } from 'react'
import { Canvas, useLoader } from '@react-three/fiber'
import { Html, Preload, OrbitControls } from '@react-three/drei'
import anime from "animejs";
import { TextureLoader } from 'three';
import SpriteGroup from '../components/SpriteGroup'

const store = [
  { name: 'outside', color: 'lightpink', position: [10, 0, -15], url: '/img/panoramas/ofis2.jpeg', link: 1 },
  { name: 'inside', color: 'lightblue', position: [15, 0, 0], url: '/img/panoramas/test1.jpg', link: 0 }
  // ...
]
const textureLoader = new THREE.TextureLoader();

function Dome({ name, position, texture, onClick }) {
  const spriteTexture = useLoader(TextureLoader, '/img/panoramas/icons8-info-100.png');
  texture.wrapS = THREE.RepeatWrapping;
  texture.repeat.x = -1;

  const [hovered, setHover] = useState(false);
  // const hover = e => e.stopPropagation() && setHover(true);
  // const unhover = e => e.stopPropagation() && setHover(false);
  function hover() {
    setHover(true);
  }
  function unhover() {
    setHover(false);
  }
  return (
    <group>
      <mesh>
        <sphereBufferGeometry args={[500, 60, 40]} />
        <meshBasicMaterial map={texture} side={THREE.DoubleSide} />

      </mesh>
      {/* <mesh position={position}>

        <sphereGeometry args={[1.25, 32, 32]} />
        <meshBasicMaterial color="white" />
        <Html center>

        </Html>
      </mesh> */}
      <sprite position={position} scale={[2, 2, 2]} onClick={onClick} onPointerOver={hover} onPointerOut={unhover}>
        <spriteMaterial map={spriteTexture} />
        <Html sprite='true' position={position} center style={{ pointerEvents: "none", display: hovered ? "block" : "none", }}>
          <div style={{ width: '100px', height: '100px' }} >
            <h1 className="text-center">Hello</h1>
          </div>
        </Html>
      </sprite>
      <SpriteGroup dome={name} />
    </group>
  )
}
function onHover(e) {

}
function Portals() {
  const [which, set] = useState(0)
  const { link, ...props } = store[which]
  const maps = useLoader(THREE.TextureLoader, store.map((entry) => entry.url)) // prettier-ignore
  return <Dome onClick={() => set(link)} {...props} texture={maps[which]} />
}

const DomeBlock = ({ nextPage }) => {
  useLayoutEffect(() => {
    let timeline = anime.timeline({
      easing: 'linear',
      autoplay: true,
    });
    timeline.add({
      targets: '#dome-content',
      easing: 'easeOutExpo',
      opacity: [0, 1],
      scale: [0.8, 1],
      duration: 1000,
    });
    timeline.add({
      targets: 'canvas',
      easing: 'linear',
      opacity: [0, 1],
      scale: [0.5, 1],
      duration: 400,
    });
    timeline.add({
      targets: '.css--fadedtext',
      easing: 'easeOutExpo',
      opacity: [0, 1],
      translateX: ["-100%", 0],
      duration: 500
    });
  }, [])
  return (
    <div className="relative min-h-screen min-w-screen">
      <div className="absolute h-full w-full">
        <Canvas frameloop="demand" camera={{ position: [0, 0, 0.1] }}>
          <OrbitControls enableZoom={false} enablePan={false} enableDamping dampingFactor={0.2} autoRotate={false} rotateSpeed={-0.5} />
          <Suspense fallback={null}>
            <Preload all />
            <Portals />
          </Suspense>
        </Canvas>
      </div>
      <div className="w-full h-full absolute pointer-events-none" style={{ background: 'radial-gradient(circle, rgba(255,255,255,0) 0%, rgba(0,0,0,1) 100%)' }} />
    </div>

  )
}
export default DomeBlock;