import * as React from "react"

import "../style/global.scss";
import LetsTalk from "../layout/LetsTalk";
import Projects from "../layout/Projects";
import HeroBlock from "../layout/HeroBlock";
import AddTripButton from "../components/elements/condButton";
import WeAreBlock from "../layout/WeAre";
import FadedText from "../components/helpers/FadedText";
import KYC from "../layout/KYC";
import anime from "animejs";
import TextScramble from "../components/helpers/TextScramble";
import Navigation from "../components/elements/Navigation";
import { useEffect, useLayoutEffect, useState } from "react";
import SEO from "../components/elements/SEO";
import Dome from "../layout/Dome";
import OurSolutions from "../layout/OurSolutions";

const IndexPage = () => {
    require("../style/hero_block.scss");
    require("../style/we_are_block.scss");
    require("../style/kyc_block.scss");
    require("../style/projects.scss");
    const [page, setPage] = useState("01");

    function getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    function scrollTop() {
        window.scroll(0,0);

    }
    const handlePageChange = (e) => {
        if (e.deltaY < 0 && window.scrollY === 0 && !window.loading) {
            if (parseInt(window.page) !== 1) {
                window.loading = true;
                setPage("0" + (parseInt(window.page) - 1))
                setTimeout(function(){
                    scrollTop()
                } ,80)
            }
        } else if (e.deltaY > 0 && (window.innerHeight + window.scrollY) >= (document.body.offsetHeight - 10) && !window.loading) {
            if (parseInt(window.page) !== 5) {
                window.loading = true;
                setPage("0" + (parseInt(window.page) + 1))
                setTimeout(function(){
                    scrollTop()
                } ,80)
            }
        }
    }
    const handlePageChangeMobile = (dir) => {
        if (dir < 0 && window.scrollY === 0 && !window.loading) {
            if (parseInt(window.page) !== 1) {
                window.loading = true;
                setPage("0" + (parseInt(window.page) - 1))
                setTimeout(function(){
                    scrollTop()
                } ,80)
            }
        } else if (dir > 0 && (window.innerHeight + window.scrollY) >= (document.body.offsetHeight - 10) && !window.loading) {
            if (parseInt(window.page) !== 5) {
                window.loading = true;
                setPage("0" + (parseInt(window.page) + 1))
                setTimeout(function(){
                    scrollTop()
                } ,80)
            }
        }
    }

    useEffect(() => {
        window.loading = true;
        window.addEventListener("wheel", handlePageChange);
        let ts;
        document.addEventListener('touchstart', function(e) {
            ts = e.touches[0].clientY;
        });

        document.addEventListener('touchmove', function(e) {
            let te = e.changedTouches[0].clientY;
            if (ts > te) {
                handlePageChangeMobile(1)
            } else {
                handlePageChangeMobile(-1)
            }
        });
        return () => {
            window.addEventListener("wheel", handlePageChange);
        };
    }, []);

    useLayoutEffect(() => {
        window.loading = true;
        window.page = page;
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            easing: 'easeOutExpo',
            autoplay: true,
            targets: '.css--anim-footer',
            translateX: ["-100%", 0],
            duration: 100,
            delay: 300
        });
        timeline.add({
            targets: '.css--page-number',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 400,
        });
        timeline.finished.then(() => {
            window.loading = false;
        })

        let elements = document.getElementsByClassName('text-scramble');
        Array.prototype.forEach.call(elements, function (el) {
            let glitch_text = new TextScramble(el)
            const next_update = () => {
                glitch_text.setText().then(() => {
                    setTimeout(next_update, 13000 + getRandomInt(0, 5000))
                })
            }
            next_update();
        });
    }, [page]);

    return (
        <main>
            <SEO />
            <div id="main-wrapper relative">
                <div className="absolute css--page-number top-12" style={{ left: "10%", zIndex: "20" }} >{page}</div>
                <Navigation parentCallback={setPage} />
                <div className="css--navbar-logo">
                    <img onClick={() => setPage('01')} src="/img/logo/logo_text_White-full.svg" style={{ width: "100%", opacity: 0.2 }} className="pt-5 md:pt-0" alt="Kalmars Logo" />
                </div>
                <nav className="fixed text-center hidden md:block py-4 px-6 bg-transparent w-full" style={{ zIndex: "300" }}>
                    <div className="w-4/5 mx-auto flex justify-evenly relative">
                        <button onClick={() => {setPage("01");scrollTop()}} className={(page === '01' ? "css--navigation-active" : "")  + " text-white font-bold"}>Discover</button>
                        <button onClick={() => {setPage("02");scrollTop()}} className={(page === '02' ? "css--navigation-active" : "")  + " text-white font-bold"}>About Us</button>
                        {/*<button onClick={() => setPage("03")} className="text-white">Cyborgs</button>*/}
                        <button onClick={() => {setPage("03");scrollTop()}} className={(page === '03' ? "css--navigation-active" : "")  + " text-white font-bold"}>Our solutions</button>
                        <button onClick={() => {setPage("04");scrollTop()}} className={(page === '04' ? "css--navigation-active" : "")  + " text-white font-bold"}>Portfolio</button>
                        <button onClick={() => {setPage("05");scrollTop()}} className={(page === '05' ? "css--navigation-active" : "")  + " text-white font-bold"}>Contact us</button>
                    </div>
                </nav>

                {page === '01' &&
                    <section>
                        <HeroBlock nextPage={() => setPage('02')} />
                    </section>
                }
                {page === '02' &&
                    <section className="pb-52">
                        <FadedText text={'DISCOVER'} direction="left" />
                        <WeAreBlock nextPage={() => setPage('03')} />
                    </section>
                }
                {/*{page === '03' &&*/}
                {/*   */}
                {/*}*/}
                {page === '03' &&
                    <section className="pb-52">
                        <FadedText text={'EXPAND'} direction="left" />
                        <OurSolutions nextPage={() => setPage('5')}/>
                    </section>
                }
                {page === '04' &&

                    <section className="pb-52">
                    <FadedText text={'DISCOVER'} direction="left" />
                    <Projects />
                    </section>
                }
                {page === '05' &&
                <section>
                    <FadedText text={'EXPAND'} direction="left" />
                    <LetsTalk />
                </section>
                }
            </div>
        </main>
    )
}

export default IndexPage
